.btn{
    border: none;
    outline: none;
    cursor: pointer;
    color: #fff;
    background-color: transparent;
    user-select: none;
    font-family: $DTW-Medium;
}

.btn.--transparent-fill{
    font-family: $DTW-Medium;
    font-size: vw(14);
    width: vw(219);
    height: vw(51);
    border: vw(1) solid #00DBE0;
    box-shadow: 0px vw(1) vw(15) rgba(0, 96, 98, 0.56);
    border-radius: vw(125);
}